import { Component } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { AuthService } from 'src/app/auth/services/auth.service';
import { requestUrl } from 'src/app/services/adminReqUrl.const';
import { CrudService } from 'src/app/services/crud.service';

@Component({
  selector: 'app-categories',
  templateUrl: './categories.component.html',
  styleUrls: ['./categories.component.scss']
})
export class CategoriesComponent {
  categoryList: any;
  pageNo: number = 1;
  addCategoryForm!: FormGroup;
  selectedCat: any;
  actionType: string = '';
  constructor(private authservice: AuthService, private fb: FormBuilder, private toastr: ToastrService,
    private crud: CrudService) {
    this.addCategoryForm = this.fb.group({
      category_name: [''],
      parent: [''],
      img_src: [''],
      description: ['']
    });
  }

  ngOnInit() {
    this.getProducts()
  }
  getProducts() {
    this.crud.getRequest(`${requestUrl.getShopCategory}${this.pageNo}`).then((res: any) => {
      if (res)
        this.categoryList = res.data;
    });
  }
  getSpeciticCategoryDetail(data: any) {
    this.actionType = 'editCategory'
    this.authservice.getSpecificCategory(data.id).subscribe((res: any) => {
      this.addCategoryForm.controls['category_name'].setValue(res.data.name)
      this.addCategoryForm.controls['parent'].setValue(res.data.id)
      this.addCategoryForm.controls['img_src'].setValue(res.data.image.src)
      this.addCategoryForm.controls['description'].setValue(res.data.description)
    },
      (err: any) => {
        this.toastr.error(err.error.detail, '', { timeOut: 4000 });
      });
  }
  deleteCategory() {
    this.authservice.deleteCategory(this.selectedCat.id).subscribe((res: any) => {
      this.toastr.success(res.message, '', { timeOut: 4000 });
      this.getProducts();
    },
      (err: any) => {
        this.toastr.error(err.error.detail, '', { timeOut: 4000 });
      });
  }
}
