import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { requestUrl } from 'src/app/services/adminReqUrl.const';
import { CrudService } from 'src/app/services/crud.service';

@Injectable()
export class OrderService {

  order = new BehaviorSubject<any>(null);

  constructor(private _crud:CrudService)
  {}

  getOrders(pageNo:number) {
    return this._crud.getRequest(`${requestUrl.getShopOrderList}${pageNo}`)
  }

  getOrderByID(id:number): Promise<any> {
    // Return already existing order as promise
    if(this.order.value && this.order.value.id == id){
      return Promise.resolve({ data:this.order.value })
    }
    return this._crud.getRequest(`${requestUrl.getOrderdetail}${id}`)
  }

  viewOrder(order:any) {
    this.order.next(order);
  }
}
